:root {
  --main-text-color-black: #000;
  --bg-color: #fff;
  --text-color: #123;
  --close-color: #123;
  --pagination-arrow-btn: #f7f7f7;
  --card-shadow: 0px 0px 15px 1px rgba(0, 0, 0, .51);
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

:root.dark {
  --bg-color: #272e34;
  --text-color: #fff;
  --close-color: #fff;
  --pagination-arrow-btn: #8c8c8c;
  --card-shadow: 2px 2px 25px 2px rgba(204, 204, 204, .25);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  display: none;
}

.no-scroll {
